<template>
    <div>
        <h3>–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è</h3>

        <TextInput @getValText="card.surname = $event" :valEdit="userInfo.surname" nameField="–§–∞–º–∏–ª–∏—è"/>
        <TextInput @getValText="card.name = $event" :valEdit="userInfo.name" nameField="–ò–º—è"/>
        <TextInput @getValText="card.patronymic = $event" :valEdit="userInfo.patronymic" nameField="–û—Ç—á–µ—Å—Ç–≤–æ"/>
        <SelectList @getValSelected="card.position = $event" :valEdit="userInfo.position" :arr=arr
                    nameField="–î–æ–ª–∂–Ω–æ—Å—Ç—å"/>
        <DateInput @getValDate="card.date_of_birth = $event" :valEdit="userInfo.date_of_birth"
                   nameField="–î–∞—Ç–∞ —Ä–æ–∂–¥–µ–Ω–∏—è"/>
        <RadioInput @getValRadio="card.gender = $event" :valEdit="userInfo.gender" :arr="gender" nameField="–ü–æ–ª"/>
        <DateInput @getValDate="card.date_employment = $event" :valEdit="userInfo.date_employment"
                   nameField="–î–∞—Ç–∞ –ø—Ä–∏–µ–º–∞ –Ω–∞ —Ä–∞–±–æ—Ç—É"/>
        <DateInput @getValDate="card.date_of_dismissal = $event" :valEdit="userInfo.date_of_dismissal"
                   nameField="–î–∞—Ç–∞ —É–≤–æ–ª—å–Ω–µ–Ω–∏—è"/>
        <CheckInput @getValCheck="card.rights = $event" :valEdit="userInfo.rights" nameField="–ù–∞–ª–∏—á–∏–µ –ø—Ä–∞–≤"/>

        <button type="button" class="btn btn-success" @click="editUser">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
    </div>
</template>

<script>
    import TextInput from '../components/TextInput.vue'
    import DateInput from '../components/DateInput.vue'
    import SelectList from '../components/SelectList.vue'
    import RadioInput from '../components/RadioInput.vue'
    import CheckInput from '../components/CheckInput.vue'

    export default {
        components: {
            TextInput, DateInput, SelectList, RadioInput, CheckInput
        },
        name: 'EditUser',
        data() {
            return {
                arr: ["–ú–µ–Ω–µ–¥–∂–µ—Ä", "–ü–∏–ª–æ—Ç", "–ü—Ä–æ–≥—Ä–∞–º–º–∏—Å—Ç", "–ê–Ω–∞–ª–∏—Ç–∏–∫", "–ì–µ–Ω–¥–∏—Ä"],
                gender: ["M", "–ñ"],
                card: {
                    surname: null,
                    name: null,
                    patronymic: null,
                    position: null,
                    date_of_birth: null,
                    gender: null,
                    date_employment: null,
                    date_of_dismissal: null,
                    rights: false,
                    id: null
                },
                userInfo: null
            }
        },
        methods: {
            editUser() {
                if (!this.checkAllFields()) {
                    alert("–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è —Å–æ –∑–≤–µ–∑–¥–æ—á–∫–∞–º–∏")
                    return
                }
                let allUser = (localStorage.allUser === undefined) ? [] : JSON.parse(localStorage.allUser);
                for (let i = 0; i < allUser.length; i++) {
                    if (allUser[i].id == this.card.id) {
                        allUser[i] = this.card
                        localStorage.allUser = JSON.stringify(allUser)
                        this.$router.push({ name: 'all'})
                    }
                }
            },

            checkAllFields() {
                // –§–∞–º–∏–ª–∏—è
                if (this.card.surname == null || this.card.surname === "") {
                    return false
                }
                // –ò–º—è
                if (this.card.name == null || this.card.name === "") {
                    return false
                }
                // –î–æ–ª–∂–Ω–æ—Å—Ç—å
                if (this.card.position == null || this.card.position === "") {
                    return false
                }
                // –î–∞—Ç–∞ —Ä–æ–∂–¥–µ–Ω–∏—è
                if (this.card.date_of_birth == null || this.card.date_of_birth === "") {
                    return false
                }
                // –ü–æ–ª
                if (this.card.gender == null || this.card.gender === "") {
                    return false
                }
                // –î–∞—Ç–∞ –ø—Ä–∏–µ–º–∞ –Ω–∞ —Ä–∞–±–æ—Ç—É
                if (this.card.date_employment == null || this.card.date_employment === "") {
                    return false
                }
                // –î–∞—Ç–∞ —É–≤–æ–ª—å–Ω–µ–Ω–∏—è
                if ((this.card.date_of_dismissal == null || this.card.date_of_dismissal === "")) {
                    return false
                }
                // –ü—Ä–æ–≤–µ—Ä–∫–∞ –¥–∞—Ç
                if (!(new Date(this.card.date_of_dismissal) > new Date(this.card.date_employment))) {
                    return false
                }
                return true
            }
        },
        beforeMount() {
            let id = this.$route.params.id
            let arr = JSON.parse(localStorage.allUser)
            for (let i = 0; i < arr.length; i++) {
                if (arr[i].id == id) {
                    this.userInfo = arr[i]
                    this.card = this.userInfo
                    break
                }
            }
        }
    }
</script>
