<template>
    <div>
        <h3>–î–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è</h3>

        <TextInput @getValText="card.surname = $event" idField="surname" nameField="–§–∞–º–∏–ª–∏—è *"/>
        <TextInput @getValText="card.name = $event" idField="name" nameField="–ò–º—è *"/>
        <TextInput @getValText="card.patronymic = $event" idField="patronymic" nameField="–û—Ç—á–µ—Å—Ç–≤–æ"/>
        <SelectList @getValSelected="card.position = $event" idField="position" :arr=arr nameField="–î–æ–ª–∂–Ω–æ—Å—Ç—å *"/>
        <DateInput @getValDate="card.date_of_birth = $event" idField="date_of_birth" nameField="–î–∞—Ç–∞ —Ä–æ–∂–¥–µ–Ω–∏—è *"/>
        <RadioInput @getValRadio="card.gender = $event" idField="gender" nameField="–ü–æ–ª *" :arr="gender"/>
        <DateInput @getValDate="card.date_employment = $event" idField="date_employment"
                   nameField="–î–∞—Ç–∞ –ø—Ä–∏–µ–º–∞ –Ω–∞ —Ä–∞–±–æ—Ç—É *"/>
        <DateInput @getValDate="card.date_of_dismissal = $event" idField="date_of_dismissal"
                   nameField="–î–∞—Ç–∞ —É–≤–æ–ª—å–Ω–µ–Ω–∏—è *"/>
        <CheckInput @getValCheck="card.rights = $event" idField="rights" nameField="–ù–∞–ª–∏—á–∏–µ –ø—Ä–∞–≤" :check="card.rights"/>

        <button type="button" class="btn btn-success" @click="addUser">–î–æ–±–∞–≤–∏—Ç—å</button>
    </div>
</template>

<script>
    import TextInput from '../components/TextInput.vue'
    import DateInput from '../components/DateInput.vue'
    import SelectList from '../components/SelectList.vue'
    import RadioInput from '../components/RadioInput.vue'
    import CheckInput from '../components/CheckInput.vue'

    export default {
        components: {
            TextInput, DateInput, SelectList, RadioInput, CheckInput
        },
        name: 'AddUser',
        props: {
            msg: String
        },
        data() {
            return {
                arr: ["–ú–µ–Ω–µ–¥–∂–µ—Ä", "–ü–∏–ª–æ—Ç", "–ü—Ä–æ–≥—Ä–∞–º–º–∏—Å—Ç", "–ê–Ω–∞–ª–∏—Ç–∏–∫", "–ì–µ–Ω–¥–∏—Ä"],
                gender: ["M", "–ñ"],
                card: {
                    surname: null,
                    name: null,
                    patronymic: null,
                    position: null,
                    date_of_birth: null,
                    gender: null,
                    date_employment: null,
                    date_of_dismissal: null,
                    rights: false,
                    id: null
                }
            }
        },
        methods: {
            addUser() {
                if (!this.checkAllFields()) {
                    alert("–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è —Å–æ –∑–≤–µ–∑–¥–æ—á–∫–∞–º–∏")
                    return
                }
                let allUser = (localStorage.allUser === undefined) ? [] : JSON.parse(localStorage.allUser);
                this.card.id = allUser.length;
                allUser.push(this.card)
                localStorage.allUser = JSON.stringify(allUser)
                this.$router.push({ name: 'all'})

            },
            checkAllFields() {
                // –§–∞–º–∏–ª–∏—è
                if (this.card.surname == null || this.card.surname === "") {
                    return false
                }
                // –ò–º—è
                if (this.card.name == null || this.card.name === "") {
                    return false
                }
                // –î–æ–ª–∂–Ω–æ—Å—Ç—å
                if (this.card.position == null || this.card.position === "") {
                    return false
                }
                // –î–∞—Ç–∞ —Ä–æ–∂–¥–µ–Ω–∏—è
                if (this.card.date_of_birth == null || this.card.date_of_birth === "") {
                    return false
                }
                // –ü–æ–ª
                if (this.card.gender == null || this.card.gender === "") {
                    return false
                }
                // –î–∞—Ç–∞ –ø—Ä–∏–µ–º–∞ –Ω–∞ —Ä–∞–±–æ—Ç—É
                if (this.card.date_employment == null || this.card.date_employment === "") {
                    return false
                }
                // –î–∞—Ç–∞ —É–≤–æ–ª—å–Ω–µ–Ω–∏—è
                if ((this.card.date_of_dismissal == null || this.card.date_of_dismissal === "")) {
                    return false
                }
                // –ü—Ä–æ–≤–µ—Ä–∫–∞ –¥–∞—Ç
                if (!(new Date(this.card.date_of_dismissal) > new Date(this.card.date_employment))){
                    return false
                }

                return true
            }
        }
    }
</script>
